export default {
  name: "adsdp",
  data() {
    return {
      show_dp: true
    }
  },
  created() {
    if (this.$store.state.hasLogin && this.$store.state.user.userinfo.is_vip) {
      this.show_dp = false;
    }
  },
  methods: {
    on_click_close: function () {
      this.show_dp = false;
      this.$forceUpdate();
      console.log('on_click_close:');
    }
  }
}
